} */

.control-box{
    display: flex;
    justify-content: space-around;
    align-items: center;
    font-size: 1.5em;
}
label {
    display: block;
    margin: 0.3em 0;
}

.numbers{
    text-align: center;
}
.numbers span{
    margin: 0 1rem;
}

.control-box button{
   border-radius: 0; 
   width: 30px;
   height: 30px;
   background-color: #fff;
   border: 1px solid gray;
   margin-bottom: 1em;
}

.maxNum-box input{
   width: 2.5em;
   height: 2.5em;
   margin-bottom: 1em;
}

.balls{
    width: 100%;
    margin: 2rem auto;
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    justify-items: center;
}

.ball{
    position: relative;
    left: 0px;
    width: 50px;
    height: 50px;
    background: #004E99;
    border-radius: 50%;
    box-shadow: 20px 30px 30px -10px rgba(0,0,0,0.2);
}
.ball>div{
    display: inline;
    width: 100%;
    height: 100%;
    border-radius: 50%;
}

.ball>div>span{
    position: absolute;
    left: 10px;
    top: 10px;
    width: 30px;
    height: 30px;
    border-radius:50%;
	text-align:center;
	line-height:35px;
	font-size:20px;
	font-weight:bold;
	background:#fff;
} 

.input-container{
    display: flex;
    flex-direction: column;
    background-color: #fff;
    border: 1px solid gray;
    border-radius: 10px;
}
.button-container {
    display: flex;
    justify-content: space-between;
    margin: 1rem;
}

.input{
    border: none;
    margin: 0.5em;
    padding: 0.5em 0.5em;
}

.input-container button{
    margin: 0.5em auto;
    padding: 0.5em 0.5em;
    background-color: #fff;
    border: 1px solid gray;
    border-radius: 10px;
}
input:focus{
    outline: none;
    border-radius: none;
}

.item-container ul{
    display: flex;
    margin: 0.5em auto;
    justify-content: space-around;
    background-color: #fff;
    align-items: center;
    list-style: none;
    font-size: 2rem;
    border: 1px solid gray;
    border-radius: 10px;
    padding: 0;
   
}
.item-container ul > li{
    display: inline-block;
     margin: 0.5em auto;
}

.item-container button{
    border: none;
}

.pick-button-box{
    margin-top: 2em;
}

.two-button-box{
    display: flex;
    justify-content: space-around;
}
.randomItem-box{
    margin: 1em auto;
    padding: 1rem 1rem;
    font-size: 2rem;
    text-align: center;
    transition: all 0.2s ease-in-out;
}

.review {
	width: 60%;
	display: flex;
	flex-direction: column;
	box-shadow: 0 2.4rem 4.8rem rgba(0, 0, 0, 0.075);
	border-radius: 11px;
	padding: 3.2rem;
}

.review-title {
	font-size: 2.4rem;
	margin-bottom: 3.2rem;
}

.review-description {
	font-size: 1.6rem;
	line-height: 1.6;
	margin-bottom: 3.2rem;
}

.review-credit {
	font-size: 1.4rem;
	color: var(grey-color);
	margin-bottom: 2.4rem;
}

/* HERO SECTION */
.section-hero {
	background-color: var(--tint-color);
	padding: 4.8rem 0 9.6rem 0;
}

.hero-container {
	max-width: 130rem;
	margin: 0 auto;
	padding: 0 3.2rem;
	display: grid;
	grid-template-columns: 1fr 1fr;
	grid-gap: 9.6rem;
	gap: 9.6rem;
	align-items: center;
}

.hero-primary span {
	color: var(--primary-color);
}

.hero-description {
	font-size: 2rem;
	line-height: 1.6;
	margin-bottom: 4.8rem;
}

.hero-image-box a {
	text-decoration: none;
	color: var(--grey-color);
}

.hero-img {
	width: 100%;
}

.btns {
	width: -webkit-max-content;
	width: -moz-max-content;
	width: max-content;
	display: flex;
	grid-gap: 10px;
	gap: 10px;
}

.appstore {
	display: flex;
	grid-gap: 8px;
	gap: 8px;
	font-size: 2.4rem;
	margin-top: 1.6rem;
}

.appstore-imgs {
	color: #ff9529;
}

/*  ABOUT SECTION */
.section-about,
.section-review {
	padding: 9.6rem 0;
}

.container-mode {
	display: grid;
	grid-template-columns: repeat(3, 1fr);
	grid-gap: 9.6rem;
	gap: 9.6rem;
}

.about-imgs {
	display: grid;
	grid-template-columns: repeat(4, 1fr);
	grid-gap: 10px;
	gap: 10px;
}

.about-img {
	width: 90%;
	border-radius: 11px;
}

/* SCREENSHOT SECTION */

.section-screenshot {
	background-color: var(--tint-color);
	padding: 9.6rem 0;
}

.footer {
	position: absolute;
	bottom: -23.4rem;
	background-color: var(--tint-color);
	width: 100%;
}

.container-footer {
	padding: 9.6rem;
	text-align: center;
}

.footer-link:link {
	color: var(--grey-color);
}

.footer-icon {
	font-size: 4.4rem;
}

.footer-text {
	font-size: 2.4rem;
}

/* GENERAL REUSABLE COMPONENTS */

.grid {
	display: grid;
	grid-gap: 9.6rem;
	gap: 9.6rem;
}

.grid--2-cols {
	grid-template-columns: repeat(2, 1fr);
}

.grid--3-cols {
	grid-template-columns: repeat(3, 1fr);
}

.grid--4-cols {
	grid-template-columns: repeat(4, 1fr);
}

.heading-primary {
	font-size: 5.2rem;
	font-weight: 700;
	line-height: 1.05;
	color: #333;
	letter-spacing: -0.5px;
	margin-bottom: 3.2rem;
}

.heading-secondary {
	font-size: 4.4rem;
	line-height: 1.2;
	margin-bottom: 9.6rem;
}

.subheading {
	display: block;
	font-size: 1.6rem;
	font-weight: 500;
	color: var(--dark-primary-color);
	text-transform: uppercase;
	margin-bottom: 1.6rem;
	letter-spacing: 0.75px;
}
.btn:link,
.btn:visited {
	display: inline-block;
	background-color: var(--primary-color);
	color: var(--white-color);
	text-decoration: none;
	font-size: 2rem;
	font-weight: 600;
	text-transform: uppercase;
	padding: 1.6rem 3.2rem;
	border-radius: 10px;
	transition: background-color 0.3s;
}

.btn--full:link,
.btn--full:visited {
	background-color: var(--primary-color);
	color: var(--white-color);
}

.btn--full:hover,
.btn--full:active {
	background-color: var(--white-color);
	box-shadow: inset 0 0 0 3px var(--primary-color);
	color: #555;
}

.btn--outline:link,
.btn--outline:visited {
	background-color: var(--white-color);
	box-shadow: inset 0 0 0 3px var(--primary-color);
	color: #555;
}

.btn--outline:hover,
.btn--outline:active {
	background-color: var(--primary-color);
	color: var(--white-color);
}

.app {
	position: relative;
	width: 100%;
	min-height: 100vh;
}

/* 
rem and em do NOT depent on html font-size in media queries
1rem = 1em = 16px
*/

/*  BELOW 1344px (Smaller Computers) */

@media screen and (max-width: 84em) {
	.hero-container {
		max-width: 120rem;
	}
}

/* BELOW 1200px (Landscape Tablets) */

@media screen and (max-width: 75em) {
	html {
		font-size: 56.25%;
	}

	.container-mode {
		grid-gap: 6.4rem;
		gap: 6.4rem;
	}
}

/* BELOW 950px (Tablets) */

@media screen and (max-width: 60em) {
	html {
		font-size: 50%;
	}

	.main-navigation__menu-btn {
		display: block;
	}

	.main-navigation__header-nav {
		display: none;
	}

	.nav-list {
		flex-direction: column;
		margin-top: 50%;
	}

	.hero-container {
		grid-template-columns: 1fr;
	}

	.hero-text-box {
		padding: 0 4.8rem;
	}

	.hero-image-box {
		text-align: center;
	}

	.hero-img {
		width: 60%;
	}

	.container-mode {
		grid-template-columns: repeat(2, 1fr);
	}
}

/* BELOW 576px (Mobile) */

@media screen and (max-width: 36em) {
	html {
		font-size: 48%;
	}

	.container-mode {
		grid-template-columns: 1fr;
	}

	.about-imgs {
		grid-template-columns: repeat(2, 1fr);
	}

	.about-img {
		margin: auto;
	}

	.btns {
		flex-direction: column;
	}
}

